/********************************************************************
***************************CSS FOR SIGNUP***************************
********************************************************************/

@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap");
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}






/*******************************************************
*************CSS STYLES FOR LOGIN & SIGNUP**************
********************************************************/
.container-back-img{
  background-image: url('./../public/images/quick-chat-app-background.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -2;
}
.container-back-color{
  background-color: #e74c3c;
  opacity: 0.7;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
}
.container {
  height: 100vh;
  width: 100%;
  align-items: center;
  display: flex;
  justify-content: center;
}

.card {
  border-radius: 10px;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.3);
  width: 400px;
  padding: 20px 0px;
  background-color: #ffffff;
  padding: 10px 30px;
}
.column{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.form .column input{
  width: calc(100% / 2 - 2px); 
} 

.card_title {
  text-align: center;
  padding: 10px;
}

.card_title h1 {
  font-size: 26px;
  font-weight: bold;
}

.form input {
  margin-bottom: 5px;
  width: 100%;
  background-color: #efefef;
  border: none;
  outline: none;
  padding: 12px 20px;
  border-radius: 4px;
}

.form button {
  background-color: #e74c3c;
  color: #ffffff;
  font-size: 16px;
  outline: none;
  border-radius: 5px;
  border: none;
  padding: 8px 15px;
  width: 100%;
  margin-top: 10px;
}

.card_terms {
  display: flex;
  align-items: center;
  padding: 10px;
}

.card_terms input[type="checkbox"] {
  background-color: #e2e2e2;
}

.card_terms span {
  margin: 5px;
  font-size: 13px;
}

.card a {
  color: #4796ff;
  text-decoration: none;
  margin-left: 5px;
}





/********************************************************************
***************************CSS FOR LOADIER**************************
********************************************************************/

.loader-overlay{
  position: fixed; /* Sit on top of the page content */
  width: 100%; /* Full width (cover the whole page) */
  height: 100%; /* Full height (cover the whole page) */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(231,76,60,0.7); /* Black background with opacity */
  z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
  align-items: center;
  display: flex;
  justify-content: center;
}
 
.loader-spinner,
.loader-spinner:after {
  box-sizing: border-box;
}
.loader-spinner {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.loader-spinner:after {
  content: " ";
  display: block;
  border-radius: 50%;
  width: 0;
  height: 0;
  margin: 8px;
  box-sizing: border-box;
  border: 32px solid white;
  border-color: white transparent white transparent;
  animation: loader-spinner 1.2s infinite;
}
@keyframes loader-spinner {
  0% {
    transform: rotate(0);
    animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
  50% {
    transform: rotate(900deg);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  100% {
    transform: rotate(1800deg);
  }
}




/*******************************************************
********CSS FOR HOME PAGE*******************************
********************************************************/
.home-page{
  background-color: #fdedec;
  display: flex;
  flex-direction: column;
  min-width: 820px;
  min-height: 100vh;
}

.main-content{
  display: flex;
  width: 90%;
  margin: 10px auto;
  padding: 10px 10px;
  flex:1;
}




/*******************************************************
********CSS FOR HEADER COMPONENT************************
********************************************************/
.app-header{
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding: 10px 30px;
  justify-content: space-between;
  border-bottom: #bbb 1px solid;
}

.app-logo{
  display: flex;
  flex-wrap: nowrap;
  color: #28282B;
  font-size: 25px;
  font-weight: bold;
}

i{
  margin: 5px 10px;
}

.app-user-profile{
  display: flex;
}

.logged-user-name{
  color: #28282B;
  font-weight: bold;
  padding-top: 10px;
  padding-right: 20px;
  margin-left: 15px;
}

.logged-user-profile-pic{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #e74c3c;
  color: white;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  vertical-align: middle;
  line-height: 40px;
  cursor: pointer;
}

.logout-btn {
  border: none;
  background: transparent;
  color: #e74c3c;
  font-size: 18px;
  cursor: pointer;
}




/*******************************************************
********CSS FOR SIDEBAR COMPONENT************************
********************************************************/
.app-sidebar{
  width: 30%;
  padding: 0px 20px;
}




/*******************************************************
********CSS FOR SEARCH COMPONENT************************
********************************************************/

.user-search-area{
  margin-bottom: 20px;
  position: relative;
}

.user-search-text{
  width: 100%;
  height: 40px;
  padding: 10px 20px;
  border: none;
  border-radius: 20px;
  color: #28282B;
  border: #ddd 1px solid;
}

.user-search-btn{
  position: absolute;
  right: 10px;
  font-size: 25px;
  color: #e74c3c;
}




/*******************************************************
********CSS FOR USER LIST COMPONENT*********************
********************************************************/
.filtered-user{
  border-bottom: #ccc 1px solid;
  padding: 10px 10px;
  color: #28282B;
  cursor: pointer;
  background-color: white;
}

.filter-user-display{
  display: flex;
  flex-wrap: wrap;
}
.filter-user-details{
  width: 60%;
}
.user-display-name{
  padding: 1px 20px;
  font-size: 16px;
  font-weight: bold;
}
.user-display-email{
  padding: 1px 20px;
  font-size: 12px;
}

.user-default-avatar, .user-profile-image, .user-selected-avatar{
  width: 50px;
  height: 50px;
  border-radius: 50%;
  font-size: 22px;
  font-weight: bold;
  text-align: center;
  vertical-align: middle;
  line-height: 50px;
}
.user-default-avatar{
  background-color: #e74c3c;
  color: white;
}

.user-start-chat{
  padding: 10px 0px;
}
.user-start-chat-btn{
  padding: 10px 5px;
  border: none;
  border-radius: 3px;
  background-color: #e74c3c;
  color: white;
}




/*******************************************************
********CSS FOR SELECTED USER***************************
********************************************************/

.selected-user{
  border-bottom: #ccc 1px solid;
  padding: 10px 10px;
  color: white;
  cursor: pointer;
  background-color: #e74c3c;
}

.user-selected-avatar{
  background-color: white;
  color: #e74c3c;
}





/*******************************************************
*************CSS STYLES FOR CHAT AREA**************
********************************************************/

.app-chat-area{
  background-color: white;
  width: 70%;
  padding: 20px 30px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  height: 85vh;
}





/*******************************************************
*************CSS STYLES FOR CHAT AREA HEADER************
********************************************************/
.app-chat-area-header{
    padding: 10px 30px;
    margin-bottom: 20px;
    border-bottom: #bbb 1px solid;
    text-align: right;
    font-weight: bold;
    color: #e74c3c;
}





/*******************************************************
********CSS STYLES FOR CHAT AREA & CHAT INPUT***********
********************************************************/
.main-chat-area{
    flex: 1;
    overflow-y: scroll;
    padding: 10px 20px;
}

.send-message-div{
    position: relative;
    margin-top: 20px;
}

.send-message-input{
    width: 100%;
    height: 40px;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    color: #28282B;
    border: #ddd 1px solid;
}

.send-message-btn, .send-emoji-btn, .send-image-btn{
    position: absolute;
    right: 10px;
    font-size: 25px;
    color: #e74c3c;
    cursor: pointer;
    border: none;
    background-color: transparent;
    margin-top: 5px;
}

.send-emoji-btn, .send-image-btn{
    right: 50px;
}

.send-image-btn {
  right: 80px;
}





/*******************************************************
***************CSS FOR CHAT MESSAGES********************
********************************************************/
.message-container{
    display: flex;
}
.send-message, .received-message{
    padding: 10px 20px;
    margin-top: 5px;
    border-radius: 10px;
    font-size: 14px;
    width: fit-content;
}

.send-message{ 
    background-color: #e74c3c;
    color: white;
    margin-left: 100px;
    border-top-right-radius: 0px;
}

.received-message{ 
    background-color: #ddd;
    color: #28282B;
    margin-right: 100px;
    border-bottom-left-radius: 0px;
}

.message-timestamp{
  width: fit-content;
  font-size: 13px;
}





/*****************************************************
***********CUSTOM SCROLLBAR FOR CHATAREA**************
******************************************************/
.main-chat-area::-webkit-scrollbar {
    width: 5px;
}

/* Track */
.main-chat-area::-webkit-scrollbar-track {
    box-shadow: inset 0 0 2px grey; 
    border-radius: 10px;
}

/* Handle */
.main-chat-area::-webkit-scrollbar-thumb {
    background: #e74c3c; 
    border-radius: 10px;
}

/* Handle on hover */
.main-chat-area::-webkit-scrollbar-thumb:hover {
    background: #e74c3c; 
}
.last-message-timestamp{
  font-size: 13px;
}
.unread-message-counter{
  text-align: center;
  float: right;
  font-size: 13px;
  font-weight: bold;
  padding-top: 3px;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: #e74c3c;
  color: white;
}

.typing-indicator {
  font-size: 13px;
  color: grey;
}





/*****************************************************
*************CSS STYLES FOR PROFILE PAGE**************
******************************************************/

.profile-page-container{
  display: flex;
  justify-content: center;
  min-width: 300px;
  width: 80%;
  color: #28282B;
  margin: 100px auto;
  padding: 20px 20px;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.profile-pic-container{
  width: 250px;
  margin-right: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.profile-info-container{
  width: max-content;
  padding: 30px 10px;
}
.user-profile-pic-upload, .user-default-profile-avatar{
  width: 240px;
  height: 240px;
  border-radius: 50%;
  color: #28282B;
  border: #ddd 5px solid;
  font-size: 120px;
  font-weight: bold;
  text-align: center;
  vertical-align: middle;
  line-height: 240px;
}

.user-profile-name{
  padding: 10px 0px;
  border-bottom: #cdcdcd 1.5px solid;
  margin-bottom: 30px;
}
.select-profile-pic-container{
  padding: 30px 0px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

/* file upload button */
input[type="file"]::file-selector-button, .upload-image-btn {
  border-radius: 4px;
  padding: 0 16px;
  height: 40px;
  cursor: pointer;
  background-color: #28282B;
  color: white;
  border: 1px solid rgba(0, 0, 0, 0.16);
  box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.05);
  margin-right: 16px;
  transition: background-color 200ms;
}

/* file upload button hover state */
input[type="file"]::file-selector-button:hover, .upload-image-btn:hover {
  background-color: #f3f4f6;
  color: #28282B;
}

/* file upload button active state */
input[type="file"]::file-selector-button:active {
  background-color: #e5e7eb;
  color: #28282B;
}





/*****************************************************
********************MEDIA QUERY***********************
******************************************************/

@media only screen and (max-width: 1100px) {
  .profile-page-container {
    width: 95%;
    padding: 10px;
  }
}

@media only screen and (max-width: 1000px) {
    .main-content {
      width: 100%;
      margin: 0;
    }

    .app-sidebar{
      width: 45%;
      padding: 0px 10px;
    }
    .app-chat-area {
      padding: 10px;
    }
}

@media only screen and (max-width: 740px) {
  .profile-page-container {
    display: block;
    margin: 30px auto;
    text-align: center;
  }
  .profile-pic-container {
    margin: auto;
    margin-top: 20px;
  }
  .profile-info-container {
    margin: auto;
  }
}

@media only screen and (max-width: 440px) {
  .profile-info-container {
    width: 100%;
  }
  .select-profile-pic-container {
    gap: 20px;
    justify-content: center;
  }
}

@media only screen and (max-width: 300px) {
  .profile-page-container {
    min-width: 300px;
    margin: 20px auto;
  }
  .profile-pic-container {
    width: 100%;
  }
  .user-profile-pic-upload, .user-default-profile-avatar {
    width: 200px;
    height: 200px;
  }
}



